.pagination {
    max-width: 200px;
    list-style-type: none;
    padding: 0;
    margin: 20px auto 0;
    color:white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.pagination__button {
    max-width: 100px;
    border: 1px solid goldenrod;
    background-color: goldenrod;
    cursor: pointer;
}

.pagination__button:hover {
    opacity: 0.5;
}